import React, { Component } from 'react'
import CommandLogger from './Components/CommandLogger'

export class App extends Component {
  render () {
    return (
      <box>
        <box ref="left" top="4%" height="60%" left="1%" width="40%" border={{type: 'line'}} style={{border:{fg:'white'}}}>
          <CommandLogger cmd='skypager serve' />
        </box>
        <box ref="left" top="66%" height="32%" left="1%" width="40%" border={{type: 'line'}} style={{border:{fg:'green'}}}>
          <terminal />
        </box>
        <box ref="right" top="4%" width="40%" left="42%">
          <CommandLogger options={{cwd:'/Users/jonathan/Skypager'}} cmd='skypager dev --proxy-target="localhost:6020" --proxy-path="/engine.io"' />
        </box>
      </box>
    )
  }
}

export default App



